<template><section><h1>popconfirm</h1>
<template>
    <coding :code="datas" title="popconfirm" content="这样做相比modal会更简洁。">
        <y-popconfirm title="是否删除该文件？" :ok-cbk="dels1">
            <y-button slot="html">hello</y-button>
        </y-popconfirm>
    </coding>
    <coding :code="data1" title="popconfirm" content="自定义按钮，可以应用更强的逻辑">
        <y-popconfirm title="是否删除该文件？" v-model="test">
            <y-button slot="html">hello</y-button>
            <div class="footer" slot="btn">
                <y-button slot="btn" type="ghost" @click.native="test = false">取消</y-button>
                <y-button slot="btn" @click.native="test = false">确认</y-button>
            </div>
        </y-popconfirm>
    </coding>
</template>

<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>slot::html</td>
<td>触发目标（必须）</td>
<td><code>slot</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>slot::btn</td>
<td>底部按钮</td>
<td><code>slot</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>trigger</td>
<td>触发事件</td>
<td><code>String</code></td>
<td><code>click</code></td>
<td><code>click</code>,<code>hover</code>,<code>focus</code></td>
</tr>
<tr>
<td>placement</td>
<td>气泡位置</td>
<td><code>String</code></td>
<td><code>top</code></td>
<td><code>top</code>,<code>top-left</code>,<code>top-right</code>,<code>bottom-left</code>,<code>bottom-right</code>,<code>left-top</code>,<code>left-bottom</code>,<code>right-top</code>,<code>right-bottom</code></td>
</tr>
<tr>
<td>okBtn</td>
<td>确认键按钮选项</td>
<td><code>Object</code></td>
<td><code>{show:true,text:&quot;确认&quot;}</code></td>
<td>-</td>
</tr>
<tr>
<td>okBtn</td>
<td>确认键按钮选项</td>
<td><code>Object</code></td>
<td><code>{show:true,text:&quot;确认&quot;}</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            test:false,
datas:
`<template>
    <y-popconfirm
        title="是否删除该文件？"
        :ok-cbk="dels1"
    >
        <y-button slot="html">hello</y-button>
    </y-popconfirm>
</template>
`,
data1:
`<y-popconfirm
    title="是否删除该文件？"
    v-model="test"
>
    <y-button slot="html">hello</y-button>
    <div class="footer" slot="btn">
        <y-button slot="btn" type="ghost" @click.native="test = false">取消</y-button>
        <y-button slot="btn" @click.native="test = false">确认</y-button>
    </div>
</y-popconfirm>`
        }
    },
    methods:{
        dels1(){
            this.$notify("删除成功")
        }
    }
}
</script>


